import { Component, Input } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';
import { map } from 'rxjs/operators';

@Component({
  selector: 'pulse-pic',
  templateUrl: './pulse-pic.component.html',
  styleUrls: ['./pulse-pic.component.scss']
})
export class PulsePicComponent {
  @Input()
  public src: string;

  @Input()
  public title: string;

  public get showText(): Observable<boolean> {
    return this._showText.asObservable().pipe(map((val) => (val && !!this.title)));
  }

  private _showText: BehaviorSubject<boolean> = new BehaviorSubject<boolean>(false);


  @Input()
  public size: 'sm' | 'md' | 'md-dyn' | 'lg' = 'lg';

  constructor() { }


  public hideTitle() {
    this._showText.next(false);
  }
  public showTitle() {
    this._showText.next(true);
  }
}
